import {  
    mapMutations,
	mapState
} from 'vuex';
export default {
	data() {
		return {
			
		};
	},
	computed:{
		...mapState(['hasLogin'])
	},
	methods:{
		...mapMutations(['logout']),
		//退出登录
		toLogout(){
			uni.showModal({
			    content: '确定要退出登录么',
			    success: (e)=>{
			    	if(e.confirm){
			    		this.toLogOut();
			    	}
			    }
			});
		},
		async toLogOut() {
			var that = this;
			this.$http.post(this.$httpApi.user.logout, {
			}).then(function(response) {
				that.logout()
				uni.switchTab({
					url: `/pages/index/index`
				})
			}).catch(function(error) {
				//这里只会在接口是失败状态返回，不需要去处理错误提示
				console.log(error);
				that.$api.msg("退出失败");
			});
		},
		navTo(url) {
			uni.navigateTo({
				url
			})
		},
	}
}
